<template>
   <div id="container"></div>
</template>
<script>
  var Highcharts = require('highcharts')
  //  Highcharts 
  require('highcharts/modules/exporting')(Highcharts)
  export default {
    mounted: function () {
       // 
      Highcharts.chart('container', {
        /** Highcharts  */
        lang: {
          printChart: '',
          contextButtonTitle: '',
          decimalPoint: '.',
          downloadJPEG: 'JPEG',
          downloadPDF: 'PDF',
          downloadPNG: 'PNG',
          downloadSVG: 'SVG'
        },
        credits: {
          enabled: false // 
        },
        chart: {
          type: 'column'                           // line
        },
        title: {
          text: ' '                 // 
        },
        xAxis: {
          categories: ['7:00', '8:00', '9:00', '10:00', '11:00', '12:00', '13:00'],
          crosshair: true
        },
        yAxis: {
          title: {
            text: ''
          },
          labels: {
            step: 1
          },
          plotLines: [{
            value: 0,
            width: 1,
            color: '#808080'
          }]
        },
        legend: {
          layout: 'vertical',
          align: 'left',
          verticalAlign: 'top',
          borderWidth: 0
        },
        series: [
          {
            name: ' ',
            color: '#74f7af',
            data: [
              {
                color: 'red',
                y: 200
              },
              {
                color: 'green',
                y: 300
              },
              {
                color: 'blue',
                y: 1300
              },
              {
                color: 'orange',
                y: 3200
              },
              {
                color: 'brown',
                y: 2300
              },
              {
                color: 'gray',
                y: 300
              },
              {
                color: 'pink',
                y: 600
              }
            ],
            tooltip: {
              valueSuffix: '',
              useHTML: true,
              headerFormat: '<span style="font-size: 12px"> {point.key}</span><br/>',
              pointFormatter: function () {
                return '<span style="color:{' + this.series.color + '}"></span>: <b>' + this.y + '</b>'
              }
            }
          }
        ]

      })
    }
  }
</script>
<style>
  div#container{width:100%;height:237px;padding-top: 6px;}
  div#container g.highcharts-legend-item{display:none;}
</style>



